import React, { ReactNode } from "react";
import BaseComponent from "../../../base/base-component";
import LineChart from "../line/line-chart";
import { ILineChartData } from "../line/line-chart-interface";
import { ICyclePercentChartProperties } from "./cycle-percent-interface";
import "./cycle-percent.scss";

export default class CyclePercentChart extends BaseComponent<ICyclePercentChartProperties> {
  public render(): ReactNode {
    const { chartPoints } = this.props;
    const lineChartData = chartPoints.map((point): ILineChartData => {
      return {
        x: point.cycle,
        y: point.percent,
      };
    });

    return (
      <LineChart
        gradientColorStops={["#C7AFF0", "#4A1D96"]}
        height={200}
        width={400}
        lineChartSerie={{
          name: "CyclePercent",
          chartData: lineChartData,
        }}
        onToolTipShow={(pointIndex: number) => {
          return `<div class="c-cycle-percent-tooltip">
                     <p>
                     Value: <strong>${lineChartData[pointIndex].y}</strong>
                    </p>
              </div>`;
        }}
      />
    );
  }
}
