import React, { ReactNode } from 'react';
import BaseComponent from '../../base/base-component';
import SolidButton from '../solid-button/solid-button';
import { SolidButtonColorSchemas } from '../solid-button/solid-button-enum';
import SvgIcon from '../svg-icon/svg-icon';
import { SvgIconPathCommand } from '../svg-icon/svg-icon-enum';
import { IImproveBoxProperties } from './improve-box-interface';
import './improve-box.scss';

export default class ImproveBox extends BaseComponent<IImproveBoxProperties> {
  protected declareTranslateCollection(): string | undefined {
    return 'improveBox';
  }

  public render(): ReactNode {
    const {
      improveBoxItems: { title, description, items },
    } = this.props;

    return (
      <div className="c-box-wrap">
        <div className="row c-improve-box">
          <div className="col-lg-6 c-improve-box__col">
            <div className="c-improve-box__wrap">
              <h3 className="c-score__title">{title}</h3>
              <p>{description}</p>
            </div>
          </div>
          <div className="col-lg-6 c-improve-box__col">
            <ul className="c-improve__list">
              {items.map((item: string, index: number) => {
                return (
                  <li className="c-improve-list__items" key={index}>
                    <span>{index + 1}</span>
                    <span>{item}</span>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
        <div className="row">
          <div className="col-12 c-improve-box__cta">
            <SolidButton colorSchema={SolidButtonColorSchemas.PURPLE}>
                {this.translate('callToAction')}
               <SvgIcon width="20" viewBox="0 0 20 14" paths={[{command: SvgIconPathCommand.ARROW_RIGHT,fill: "#4A1D96"}]} />
              </SolidButton>
          </div>
        </div>
      </div>
    );
  }
}
