import React, { ReactNode } from "react";
import BaseComponent from "../../../base/base-component";
import CyclePercentChart from "../../charts/cycle-percent/cycle-percent";
import PercentBox from "../../percent-box/percent-box";
import { IDetailsItemProperties } from "./details-item-interface";

export default class DetailsItem extends BaseComponent<IDetailsItemProperties> {
  protected declareTranslateCollection(): string {
    return "detailItems";
  }

  public render(): ReactNode {
    const { item, showAverage = true } = this.props;
    const {
      value,
      averageValue,
      isPercent = true,
      title,
      description,
      chartData,
    } = item;

    return (
      <div className="row c-overall-score__row">
        <div className="col-lg-3 c-overall__col">
          <div className="c-criteria-grade__wrap">
            <PercentBox boxValue={value} isDecimal={!isPercent} />
            <div className="c-criteria__info">
              <span className="c-criteria-grade__overall">{title}</span>
              {showAverage && (
                <span className="c-criteria-grade__average">
                  {`${this.translate("average")}:${averageValue}${
                    isPercent ? " %" : " "
                  }`}
                </span>
              )}
            </div>
          </div>
        </div>
        <div className="col-lg-4 c-overall__col">
          <div className="c-details__desc">{description}</div>
        </div>
        <div className="col-lg-5 c-overall__col">
          <div className="c-score__chart">
            <CyclePercentChart chartPoints={chartData} />
            <div className="c-overall__sub">Last 5 Election Cycles</div>
          </div>
        </div>
      </div>
    );
  }
}
