import React, { ReactNode } from 'react';
import 'swiper/css';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper';
import BaseComponent from '../../../base/base-component';
import SimilarStatesItem from './item/item';
import { ISimilarStatesItem } from './item/item-interface';
import { ISimilarStatesProperties } from './similar-states-interface';
import './similar-states.scss';
import SvgIcon from '../../svg-icon/svg-icon';
import { SvgIconPathCommand } from '../../svg-icon/svg-icon-enum';


export default class SimilarStates extends BaseComponent<ISimilarStatesProperties> {
  protected declareTranslateCollection(): string {
    return 'similarState';
  }

  public render(): ReactNode {
    const { lesserScoreStates, activeState, greaterScoreStates } = this.props;

    return (
      <div className="c-box-wrap c-similar-states">
        <div className="c-section c-section--arrow">
          <h3 className="c-section__title">{this.translate('similarState')}</h3>
          <div className="c-section-arrow">
            <button className="c-btn is-transparent c-similar-arrow js-similar-prev" aria-label='Prev'>
              <SvgIcon
                width="20"
                viewBox="0 0 20 14"
                paths={[
                  {
                    command: SvgIconPathCommand.SIMILAR_ARROW_LEFT,
                    fill: '#0D0C0D',
                  },
                ]}
              />
            </button>
            <button className="c-btn is-transparent c-similar-arrow js-similar-next" aria-label='Next'>
              <SvgIcon
                width="20"
                viewBox="0 0 20 14"
                paths={[
                  {
                    command: SvgIconPathCommand.SIMILAR_ARROW_RIGHT,
                    fill: '#0D0C0D',
                  },
                ]}
              />
            </button>
          </div>
        </div>
        <Swiper
          className="c-similar__states"
          modules={[Navigation]}
          spaceBetween={16}
          slidesPerView={3}
          preventClicksPropagation={false}
          preventClicks={false}
          navigation={{
            nextEl: '.js-similar-next',
            prevEl: '.js-similar-prev'
          }}
          breakpoints={{
            480: {
              slidesPerView: 4,
            },
            768: {
              slidesPerView: 6,
            },
            992: {
              slidesPerView: 6,
              spaceBetween: 23,
            },
          }}
          onSlideChange={() => console.log('slide change')}
          onSwiper={(swiper) => console.log(swiper)}
        >
          {lesserScoreStates
            .reverse()
            .map((item: ISimilarStatesItem, index: number) => {
              return (
                <SwiperSlide className="c-similar-state__items" key={index}>
                  <SimilarStatesItem item={item} />
                </SwiperSlide>
              );
            })}
          <SwiperSlide className="c-similar-state__items">
            <SimilarStatesItem item={activeState} isActive={true} />
          </SwiperSlide>
          {greaterScoreStates.map((item: ISimilarStatesItem, index: number) => {
            return (
              <SwiperSlide className="c-similar-state__items" key={index}>
                <SimilarStatesItem item={item} />
              </SwiperSlide>
            );
          })}
        </Swiper>
      </div>
    );
  }
}
